#page-bots-wallets {
  .rotate {
    animation: spin 2s linear infinite;
  }

  @keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
    }
  }

  $dominant-grey: #3d3d3d;
  $secondary-grey: #1e1e1e;
  $table-radius: 10px;

  margin: 20px 50px;
  padding-bottom: 50px;

  .options {
    justify-content: space-between;

    .wallet {
      font-size: 22px;
      font-weight: 600;
      letter-spacing: 1.2px;
    }

    .actions {
      gap: 20px;
      justify-content: space-between;

      div {
        background-color: $dominant-grey;
        border: 1px solid black;
        padding: 7px 12px;
        border-radius: 9px;
        cursor: pointer;
        transition: background-color 0.3s;
      }

      div:hover {
        background-color: $secondary-grey;
      }
    }
  }

  .header-divider {
    margin: 20px 0 30px 0;
  }

  .wallet-divison {
    background-color: #eee;
    border: 0 none;
    color: #eee;
    height: 1px;
  }

  .wallets {
    width: 100%;

    .table {
      width: 100%;
      border-spacing: 0;

      .reload-balance {
        cursor: pointer;
      }

      .no-wallets {
        border-bottom-left-radius: $table-radius;
        border-bottom-right-radius: $table-radius;

        div {
          display: flex;
          flex-direction: column;
          height: 55vh;
        }

        img {
          width: 220px;
        }
      }

      tr {
        td {
          background-color: $dominant-grey;

          div {
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }

        .name {
          padding: 20px 0;
        }

        .address {
          cursor: pointer;
        }

        p {
          text-align: center;
        }
      }

      .last-tr {
        td:first-child {
          border-bottom-left-radius: $table-radius;
        }

        td:last-child {
          border-bottom-right-radius: $table-radius;
        }
      }

      thead th {
        margin: 0;
        padding: 20px 0;
        background-color: $secondary-grey;
      }

      thead th:first-child {
        border-top-left-radius: $table-radius;
      }

      thead th:last-child {
        border-top-right-radius: $table-radius;
      }
    }
  }
}

.add-wallet-modal {
  .add-button {
    cursor: not-allowed;
    opacity: 0.5;

    &.active {
      cursor: pointer;
      pointer-events: all;
      opacity: 1;
    }
  }

  .form {
    .options {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      cursor: pointer;

      p {
        opacity: 0.8;
        font-size: 20px;
        color: grey;

        transition: all 0.2s;
      }

      .active {
        opacity: 1;
        color: white;

        text-decoration: underline;
        text-decoration-thickness: 2px;
        text-underline-offset: 5px;
      }
    }

    .inputs {
      margin: 40px 0;
      display: flex;
      flex-direction: column;
      gap: 20px;

      p {
        margin-bottom: 5px;
      }
    }
  }
}

.export-wallet-modal {
  .export-btn {
    opacity: 0.8;
    cursor: not-allowed;
    transition: all 0.2s;

    &.active {
      opacity: 1;
      cursor: pointer;
    }
  }

  .alert {
    margin-top: 10px;
  }

  input,
  select {
    color: white;
  }

  option {
    background-color: black !important;
  }

  .rmsc {
    margin-top: 15px;

    --rmsc-main: #4285f4 !important;
    --rmsc-hover: #1d1d1d !important;
    --rmsc-selected: #212121 !important;
    --rmsc-border: #ccc !important;
    --rmsc-gray: #aaa !important;
    --rmsc-bg: #2e2e30 !important;
    --rmsc-p: 10px !important;
    --rmsc-radius: 4px !important;
    --rmsc-h: 38px !important;
  }
}

.transfer-wallet-modal {
  .add-button {
    cursor: not-allowed;
    opacity: 0.5;
    transition: all 0.2s;

    &.active {
      cursor: pointer;
      opacity: 1;
    }
  }

  .form {
    .modal-types {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      cursor: pointer;

      p {
        opacity: 0.8;
        font-size: 20px;
        color: grey;

        transition: all 0.2s;
      }

      .active {
        opacity: 1;
        color: white;

        text-decoration: underline;
        text-decoration-thickness: 2px;
        text-underline-offset: 5px;
      }
    }

    .inputs {
      input,
      select {
        color: white;
      }

      option {
        background-color: black !important;
      }

      .rmsc {
        --rmsc-main: #4285f4 !important;
        --rmsc-hover: #1d1d1d !important;
        --rmsc-selected: #212121 !important;
        --rmsc-border: #ccc !important;
        --rmsc-gray: #aaa !important;
        --rmsc-bg: #2e2e30 !important;
        --rmsc-p: 10px !important;
        --rmsc-radius: 4px !important;
        --rmsc-h: 38px !important;
      }

      .fixed {
        display: flex;
        flex-direction: column;
        gap: 10px;

        .wallets {
          display: flex;
          flex-direction: column;
          gap: 5px;
          margin-top: 25px;
        }

        .total {
          margin-top: 20px;
        }
      }

      .custom {
        margin-top: 20px;

        .wallets {
          display: flex;
          flex-direction: column;
          gap: 20px;

          .remove {
            cursor: not-allowed;
            opacity: 0.8;
            transition: 0.2s;

            &.active {
              cursor: pointer;
              opacity: 1;
            }
          }
        }

        .add-wallet-btn {
          margin: 15px 0 20px 0;
        }
      }
    }
  }
}

.confirm-delete-wallet-modal {
  .modal-body {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
}
